<template>
	<div class="classified">
           

		<div class="header">
            
			<span class="back"></span>
			<span class="search"><i></i>请输入商品名</span>
		</div>
	    <div class="screening">
             <Topmenu :child-msg="msg" class="Topmenu"></Topmenu>
	    	<span @click="listsort('total','percent')" :class="{on:total}">综合</span>
	    	<span @click="listsort('commentstate','comment')" :class="{on:commentstate}">销量</span>
	    	<span @click="listsort('pricestate','price')" :class="{on:pricestate}">价格</span>
	    	<span :class="{active:isActive}" @click="toggle">☷</span>
	    </div>
	    <ul id="items" :class="{active:isActive}">
	       <li  :data-id="item.id" v-for="item in list" :key="item.id">
	    	 <img :src="item.imgurl">
	    	 <div class="info">
	    	   <p class="title">{{item.title}}</p>
	    	   <strong class="price">￥<span>{{item.price}}</span></strong>
	    	   <p class="praise">
	    	   	<span>{{item.comment}}条评论</span>
	    	   	<span>{{item.percent}}%好评</span>
	    	   </p>
	    	 </div>
	    </li> 
	    </ul>
	</div>
</template>

<script>
import Topmenu from '@/components/topmenu/index.vue';
	export default{
		
		data(){
		  return{
             msg: '商城',
             isActive:false,
		  	 list:[
                   {
                       title:'123',
                       price:'100',
					   comment:'11111',
					   imgurl:"static/img/works1.795c64c.jpg",
                       percent:'56'
                   },
                    {
                       title:'123',
                       price:'90',
					   comment:'345',
					   imgurl:"static/img/works2.26edb51.jpg",
                       percent:'53'
                   },
                    {
                       title:'123',
                       price:'20',
					   comment:'45634',
					   imgurl:"static/img/works3.3befc72.jpg",
                        percent:'36'
                   },
                    {
                       title:'123',
                       price:'80',
					   comment:'6666',
					   imgurl:"static/img/works4.3eec536.jpg",
                        percent:'88'
                   },
                    {
                       title:'123',
                       price:'80',
					   comment:'8888',
					   imgurl:"static/img/works5.e01a32a.jpg",
                        percent:'56'
                   },
                    {
                       title:'123',
                       price:'80',
					   comment:'9999',
					   imgurl:"static/img/works1.795c64c.jpg",
                       percent:'90'
                   },
                    {
                       title:'123',
                       price:'80',
					   comment:'34534523',
					   imgurl:"static/img/works1.795c64c.jpg",
                       percent:'100'
                   },
               ],
		  	 pricestate:false,
               commentstate:false,
               total:false

		  }
		},
        components:{
            Topmenu
        },
        mounted(){
          this.getList();
        },
		
		methods:{
			getList(){
                //  this.$http.get("http://localhost:4000/productlist").then(res=>{
                //      this.list=res.data.data;
                //  })
            },

            listsort(state,key){
                
               if(this[state]){
                
               	  this.list.sort(function(a,b){
               	  	  return parseFloat(a[key]) > parseFloat(b[key])
               	  })
               }else{
               	  this.list.sort(function(a,b){
               	  	  return parseFloat(a[key]) < parseFloat(b[key])
               	  })
               }
               this[state]=!this[state];
               
			},
			
			toggle(){
              this.isActive=!this.isActive
			}
		}
	}
</script>

<style lang='scss'>
.Topmenu{
    height:0.9rem;
	width:100%;
	position:fixed;
	left:0;
	top:0;
	overflow:hidden;
}
.classified .header{
	height:0.9rem;
	width:100%;
	position:fixed;
	left:0;
	top:0;
	background:#e30b20;
	overflow:hidden;
}
.classified .header .back{
	float: left;
	margin:0.15rem 0 0 0.2rem;
    width: 0.6rem;
	height: 0.6rem;
	/* background: url(../../../static/images/back.png) no-repeat; */
	background-size: cover;
}
.classified .search{
    display:block;
    width:5.75rem;
    height:0.6rem;
    line-height:0.6rem;
    margin:0.15rem 0 0 0.3rem;
    background:#fff;
    float:left;
    text-align:center;
    border-radius:0.05rem;
    color:#ccc;
    position:relative;
}
.classified .search i{
	position:absolute;
	left: 1.3rem;
	top:0.1rem;
	width:0.4rem;
	height:0.4rem;
	/* background: url(../../../static/images/search.png) no-repeat; */
	background-size: cover;
}
.classified{
	width: 100%;
    background: #f4f5f9;
    overflow: hidden;
}
.classified .screening{
	width:100%;
	height:0.9rem;
	line-height:0.9rem;
	background:#fff;
    overflow:hidden;
    position:fixed;
    top:0.9rem;
    left:0;
    .on{
        color:firebrick;
    }
}
.classified .screening span{
	float: left;
	width:25%;
	text-align: center;
}
/* .classified .screening span:nth-of-type(4){
    width: 0.45rem;
	height: 0.45rem;
	margin:0.2rem 0 0 0.7rem; 
	background: url(../../../static/images/list_icon.png) no-repeat;
	background-size: cover;
}
.classified .screening span:nth-of-type(4).active{
	width: 0.45rem;
	height: 0.45rem;
	margin:0.2rem 0 0 0.7rem;
	/* background: url(../../../static/images/list_icon_active.png) no-repeat;
	background-size: cover;
} */
#items{
	overflow:hidden;
	padding:1.9rem 0 1.2rem;
    max-width: 7.5rem;
    margin: auto;
}

#items li{
	float:left;
	width:3.72rem;
	background:#fff;
	margin-bottom:0.06rem;
}
#items.active li{
	width:7.5rem;
	padding:0.2rem 0;
	margin-bottom:0;
	border-bottom:1px solid #f4f5f9;
}
#items li:nth-of-type(2n){
	float: right;
}
#items li img{
  display:block;
  width:3.5rem;
  height:3.5rem;
  margin:0 auto;
}
#items.active li img{
	float:left;
	width:2.5rem;
    height:2.5rem;
    margin-left:0.1rem;
}
#items.active li .info{
	float:left;
	width:4.5rem;
}
#items li .title{
	color:#333;
	font-size:0.26rem;
	padding: 0.25rem 0 0 0.15rem;
	overflow: hidden;
    line-height:0.3rem;
    text-overflow: ellipsis;
    white-space: nowrap;
}
#items.active li .title{
    font-size: 0.3rem;
    line-height:0.4rem;
    height:0.8rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    display: box;
    -webkit-line-clamp: 2;
    white-space:normal;
    -webkit-box-orient: vertical;
    margin-bottom:0.3rem;
}
#items li .price{
	color:#e30b22;
	display:block;
    padding:0.2rem 0 0.2rem 0.15rem;
}
#items.active li .price{
	font-size:0.34rem;
}
#items li .praise{
	color:#85868a;
	font-size:0.22rem;
	padding:0 0 0.15rem 0.15rem;
	overflow:hidden;
}
#items li .praise span{
	float: left;
	margin-right: 0.3rem;
}

</style>


